<template>
  <!-- 模块功能管理 -->

  <v-card class="moduleFun"
          v-if="$route.name === 'module_func_manage'">
    <!-- 增加按钮 -->
    <mrc_ess_addBtn style="top: -30px"
                    v-btnAuth="['add-module','add-function']"
                    :list='addList'></mrc_ess_addBtn>
    <v-skeleton-loader :boilerplate="false"
                       type="table"
                       :tile="false"
                       :loading="showSkeleton"
                       transition="fade-transition"
                       class="mx-auto">
      <v-card-text>

        <product v-for="(item,index) in productData"
                 :key='index'
                 @delData='delData'
                 :obj='item'></product>

      </v-card-text>
    </v-skeleton-loader>
  </v-card>
  <router-view v-else></router-view>
</template>

<script> 
import { get_list } from '@/service/api/func_module'
import product from '@/components/nested_structure/product'
export default {
  components: { product },
  props: {},
  data () {
    return {
      showSkeleton: false,
      productData: {}
    };
  },
  computed: {
    addList () {
      return [
        {
          name: this.$t('label_A_new_module'),
          page_name: 'module_add',
          show: this.$store.state.btnPermission.includes('add-module')
        },
        {
          name: this.$t('label_New_features'),
          page_name: "add_func",
          show: this.$store.state.btnPermission.includes('add-function')
        }
      ]
    }
  },
  created () { },
  async mounted () {
    if (this.$route.name == 'module_func_manage') {
      this.showSkeleton = true
      const list = await get_list()
      this.productData = list.data
      this.showSkeleton = false
    }

  },
  methods: {
    delData (uuid) {
      this.Recursion(this.productData, uuid)
    },
    Recursion (arr, uuid) {
      if (arr.length <= 0) return ''
      arr.forEach((item, index) => {
        if (item.uuid == uuid) {
          arr.splice(index, 1)
        } else {
          if (item.sub) {
            this.Recursion(item.sub, uuid)
          }

        }
      })
    }
  },
  watch: {
    async '$route' (val) {
      if (val.name === 'module_func_manage') {
        this.showSkeleton = true
        const list = await get_list()
        this.productData = list.data
        this.showSkeleton = false
      }
    }
  },
};
</script>

<style scoped>
.moduleFun {
  margin-top: 50px;
}
.btnGroup {
  position: relative;
}
.addBtn {
  color: #fff;
  position: absolute;
  top: -80px;
  right: 10px;
  transform: rotateZ(45deg);
}
</style>
