<template>
  <div>
    <div class="product">
      <div class="pro_title"
           :style="{color: $vuetify.theme.defaults.light.primary}">{{INT(obj.names)}}</div>
      <div class="pro_center">{{obj.shortcode}}</div>
      <div class="action">
        <menu-list :list='list'
                   @handleClick='handleClick'>
          <template v-slot:showAction>
            <v-icon>mdi-dots-vertical</v-icon>
          </template>
        </menu-list>
        <!-- <v-menu offset-y
                transition="scale-transition">
          <template v-slot:activator="{ on }">
            <v-icon v-on="on">mdi-dots-vertical</v-icon>
          </template>
          <v-list>
            <v-list-item @click="edit">
              <v-list-item-title>{{$t('label_Edit')}}</v-list-item-title>
            </v-list-item>
            
          </v-list>
        </v-menu> -->
      </div>
    </div>
    <!-- <one-three
      :list='obj.sub'
      :level='0'
    ></one-three> -->
    <one v-for="item in obj.sub"
         :key='item.uuid'
         @delData='delData'
         :obj='item'
         :level='0'></one>
  </div>
</template>

<script>
import one from './one'
export default {
  components: { one },
  props: {
    obj: {
      type: Object
    }
  },
  data () {
    return {
      noClick: false
    };
  },
  created () { },
  mounted () { },
  methods: {
    delData (uuid) {
      this.$emit('delData', uuid)
    },
    handleClick ({ index }, item) {
      if (index == 0) {
        this.$store.commit('set_subPage_name', this.obj.names)
        this.$store.commit('set_uuid', this.obj.uuid)
        this.$store.commit('set_iconstatus', true)  //行内进图标显示
        this.$router.push({
          name: 'product_detail',
          params: {
            uuid: this.obj.uuid,
            name: this.obj.names
          }
        })
      }
      // 编辑
      if (index == 1) {
        this.edit()
      }
    },
    edit () {
      this.$store.commit('set_subPage_name', this.obj.names)
      this.$store.commit('set_uuid', this.obj.uuid)
      this.$store.commit('set_iconstatus', false)
      this.$router.push({
        name: 'product_detail',
        params: {
          uuid: this.obj.uuid,
          name: this.obj.names
        }
      })
    },
    del () {

    },

  },
  computed: {
    list () {
      return [
        {
          name: this.$t('label_View'),
          show: true
        },
        {
          name: this.$t('label_Edit'),
          show: true
        }
      ]
    }
  },
  watch: {},
};
</script>

<style scoped>
.product {
  width: 100%;
  height: 60px;
  border: 1px solid #dedede;
  border-radius: 8px;
  padding-left: 10px;
  display: flex;
  font-size: 17px;
  align-items: center;
  justify-content: space-between;
}
.product .pro_title {
  margin-left: 10px;
  width: 34%;
  text-align: left;
  font-weight: 600;
  font-size: 18px;
}
.product .action {
  width: 50px;
}
</style>
